<template>
  <div class="one">
    <div class="font" data-aos="new-animation" data-aos-duration="3000">MrNobody</div>
    <div ref="oneRef" style="width: 100%; height: 100%"></div>
  </div>
</template>


<script>
import * as THREE from 'three'//导入样式
import FOG from 'vanta/src/vanta.fog'//导入动态样式逻辑
export default {
  data() {
    return {};
  }
  ,
  mounted() {
    this.vantaEffect = FOG({
      el: this.$refs.oneRef,
      THREE: THREE
    })
    // 修改颜色时 cells 需要全大写字母 可生效
    VANTA.FOG({
      el: this.$refs.oneRef,
      mouseControls: true,
      touchControls: true,
      gyroControls: false,
      minHeight: 200.00,
      minWidth: 200.00,
      highlightColor: 0xF7A8A8,
      midtoneColor: 0xFFB6C1,
      lowlightColor: 0xFFC3A0,
      baseColor: 0x6d6d6d,
      speed: 2.00
    });
  }
  ,
  beforeDestroy() {
    if (this.vantaEffect) {
      this.vantaEffect.destroy()
    }
  }
}
</script>

<style scoped lang="less">
.one {
  position: relative;
  width: 100%;
  height: 100%;
  //background-image: url('@/assets/img/bgu.jpg');
  //background-size: cover;
  color: white;
  display: flex;
  align-items: center;
  justify-content: center;
  font-size: 60px;
  margin-right: 10px;
  /* 粉笔主题*/
  font-family: 'FrederickatheGreat', Mulish, -apple-system, "PingFang SC", "Microsoft YaHei", sans-serif;

  .font {
    position: absolute;
    top: 50%;
    left: 50%;
    transform: translate(-50%, -50%);
    z-index: 1000;
  }
}

</style>